import React, { useEffect, useState } from "react";
import styled from "styled-components";
import { useDispatch, useSelector } from "react-redux";
import { get } from "lodash";
import { makeStyles } from "@material-ui/core/styles";

import Table from "../../../CommonComponents/Table";
import Modal from "../../../CommonComponents/Modal";

import { getContacts } from "../../store/employeeStore/actions";

import SearchBarComponent from "../SearchBarComponent";
import { Container } from "../DashboardContainer";

import {
  fontH4PrimaryRoboto,
  font14PrimaryRoboto400,
  fontH2PrimaryRoboto,
} from "../../themes/typos";
import CloseOutlinedIcon from "@material-ui/icons/CloseOutlined";

const useStyles = makeStyles({
  root: {
    top: "30px",
    right: "30px",
    position: "absolute",
    cursor: "pointer",
  },
});

export const QuoteContainer = styled.div`
  position: relative;
  padding: 30px;
`;

export const Title = styled(fontH4PrimaryRoboto)``;
export const Text = styled(font14PrimaryRoboto400)``;
export const Heading = styled(fontH2PrimaryRoboto)``;

const DashboardContacts = () => {
  const dispatch = useDispatch();
  const classes = useStyles();
  const [selectedContact, setSelectedContact] = useState(null);

  const store = useSelector((state) => get(state, ["employeeStore"], {}));
  const contacts = get(store, "contacts", []);

  const loader = get(store, "loader", false);

  useEffect(() => {
    dispatch(getContacts());
  }, []);

  const resetModal = () => {
    setSelectedContact(null);
  };

  const onClickCrossIcon = () => {
    setSelectedContact(null);
  };

  const getModalContent = () => {
    return (
      <QuoteContainer>
        <CloseOutlinedIcon
          className={classes.root}
          onClick={onClickCrossIcon}
        />
        <Heading>Quote details</Heading>
        <Title>Service name</Title>
        <Text>{selectedContact.serviceName}</Text>
        <hr />
        <Title>Name</Title>
        <Text>{selectedContact.name}</Text>
        <Title>Email</Title>
        <Text>{selectedContact.email}</Text>
        <Title>Phone number</Title>
        <Text>{selectedContact.phoneNumber}</Text>
        <Title>Message</Title>
        <Text>{selectedContact.message}</Text>
      </QuoteContainer>
    );
  };

  return (
    <Container loader={loader} noSearchBar noAddButton>
      <>
        {selectedContact && (
          <Modal
            open={!!selectedContact}
            handleClose={() => {
              resetModal();
            }}
          >
            {getModalContent()}
          </Modal>
        )}
        <Table
          noCount
          page={0}
          data={contacts}
          columnKeys={[
            { id: "name", label: "Name" },
            { id: "email", label: "Email" },
            { id: "phoneNumber", label: "Phone number" },
            { id: "message", label: "Message" },
          ]}
          //   rowsPerPage={}
          //   count={}

          onSelectTableRow={(row) => {
            setSelectedContact(row);
          }}
          gotoNextPage={(page) => {
            // dispatch(getDashboardPageJobsList({ page }));
          }}
        />
      </>
    </Container>
  );
};

export default DashboardContacts;
