import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { get } from "lodash";

import Table from "../../../CommonComponents/Table";

import {
  getEmployeesList,
  searchEmployeesList,
} from "../../store/employeeStore/actions";

import {
  deleteEmployeeById,
  getEmployee,
} from "../../services/ApiService/actions";

import { Container } from "../DashboardContainer";
import AlertDialog from "../../../CommonComponents/AlertDialog";
import EmployeeDetailsView from "../../components/EmployeeDetailsView";

const DashboardEmployeesView = () => {
  const dispatch = useDispatch();
  const [menuItem, setMenuItem] = useState(null);
  const [selectedEmployee, setSelectedEmployee] = useState(null);

  const store = useSelector((state) => get(state, ["employeeStore"], {}));
  const employees = get(store, "employees", []);
  const loader = get(store, "loader", false);

  const getEmployees = async () => {
    await dispatch(getEmployeesList({ page: 0 }));
  };

  useEffect(() => {
    getEmployees();
  }, []);

  const resetModal = () => {
    setMenuItem(null);
    setSelectedEmployee(null);
  };

  const deleteEmployee = async () => {
    await deleteEmployeeById({ id: selectedEmployee.id });
    getEmployees();
  };

  const renderDeleteAlert = () => {
    if (menuItem === "Delete") {
      return (
        <AlertDialog
          handleAgreeClick={() => {
            deleteEmployee();
            resetModal();
          }}
          handleDisagreeClick={() => {
            resetModal();
          }}
          text="Are you sure, you want to delete this Employee?"
        />
      );
    }
  };

  const updateSelectedEmployee = async () => {
    const emp = await getEmployee({ id: selectedEmployee.id });
    setSelectedEmployee(emp);
  };

  const renderContent = () => {
    switch (menuItem) {
      case "Edit":
      case "Add":
      case "View":
        return (
          <EmployeeDetailsView
            employeeData={selectedEmployee}
            updateSelectedEmployee={updateSelectedEmployee}
            handleBackButtonClick={() => {
              getEmployees();
              resetModal();
            }}
          />
        );
      default:
        return renderEmployeeList();
    }
  };

  const handleRequestSearch = async (param) => {
    if (param) {
      dispatch(searchEmployeesList({ searchParam: param, page: 0 }));
    } else {
      getEmployees();
    }
  };

  const handleCancelSearch = () => {
    getEmployees();
  };

  const displaySearchBar = () =>
    menuItem === "Edit" || menuItem === "Add" || menuItem === "View";

  const getModifiedData = () => {
    return employees.map((emp) => ({
      ...emp,
      fullName: `${emp.firstName} ${emp.lastName}`,
    }));
  };

  const renderEmployeeList = () => {
    return (
      <>
        {renderDeleteAlert()}
        <Table
          data={getModifiedData()}
          dashboard
          noCount
          noPublish
          columnKeys={[
            { id: "id", label: "Employee Id" },
            { id: "fullName", label: "Name" },
            { id: "designation", label: "Designation" },
            { id: "personalEmailId", label: "Email Id" },
            { id: "skills", label: "Skills" },
            { id: "personalNumber", label: "Phone Number" },
          ]}
          rowsPerPage={employees.numberOfElements}
          count={employees.totalPages}
          totalElements={employees.totalElements}
          onSelectMenuItem={({ menu, row }) => {
            setMenuItem(menu);
            setSelectedEmployee(row);
          }}
          onSelectTableRow={(row) => {
            setMenuItem("View");
            setSelectedEmployee(row);
          }}
        />
      </>
    );
  };

  return (
    <Container
      loader={loader}
      handleCancelSearch={handleCancelSearch}
      handleRequestSearch={handleRequestSearch}
      handleAddButtonClick={() => {
        setMenuItem("Add");
        setSelectedEmployee({});
      }}
      noSearchBar={displaySearchBar()}
      noAddButton={displaySearchBar()}
    >
      {renderContent()}
    </Container>
  );
};

export default DashboardEmployeesView;
