/* eslint-disable no-unused-vars */
import React, { useEffect, useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import CloseOutlinedIcon from "@material-ui/icons/CloseOutlined";

import { PropTypes } from "prop-types";

import {
  checkSubmitButtonDisability,
  handleButtonIsDisabled,
  handleHasError,
} from "../../utils/formUtils";
import {
  EMAIL_ERROR_TEXT,
  EMPLOYEE_DETAILS,
  INVALID_DATE,
  MIN3CHARS_ERROR_TEXT,
  PHONENUMBER_ERROR_TEXT,
} from "../../constants";
import {
  TextInput,
  PrimaryButton,
  UploadButton,
  DatePicker,
} from "../../../CommonComponents";
import colors from "../../themes/colors";
import { ModalInputsWrapper, Fields, YourReferralText } from "..";

const useStyles = makeStyles({
  field: {
    margin: "15px 0",
    width: "100%",
    "& .MuiFilledInput-root ": {
      fontSize: "14px",
      background: `#F0F0F0`,
    },
    "& .MuiList-root ": {
      "z-index": 1000001,
    },
    "& .MuiInputLabel-filled": {
      fontSize: "14px",
    },
  },
  root: {
    top: "30px",
    right: "30px",
    position: "absolute",
    cursor: "pointer",
  },
  button: {
    marginTop: "15px",
    width: "90px",
    fontSize: "14px",
  },
});

const { name, email, phoneNumber, message } = EMPLOYEE_DETAILS.CONTACT_IDS;

const QuoteOrContanctForm = ({
  onClickCrossIcon,
  successMessageId = "",
  formId = "",
  onClickSubmitButton,
  empData = {},
}) => {
  const classes = useStyles();
  const [error, setError] = useState({});
  const [data, setData] = useState(empData);

  const handleError = (id, value) => {
    const hasError = handleHasError(id, value);
    setError((err) => {
      return {
        ...err,
        [id]: hasError,
      };
    });
  };

  const handleChange = (e) => {
    const { id, name, value } = e.target;
    const key = id || name;
    handleError(key, value);
    setData({
      ...data,
      [key]: value,
    });
  };

  const handleClick = () => {};
  return (
    <ModalInputsWrapper>
      <form id={formId}>
        <Fields>
          <TextInput
            id={name}
            label="Name"
            onChange={handleChange}
            error={error[name]}
            cssClass={classes.field}
            helperText={MIN3CHARS_ERROR_TEXT}
            defaultValue={empData[name]}
          />
          <TextInput
            id={email}
            label="Email"
            onChange={handleChange}
            error={error[email]}
            cssClass={classes.field}
            helperText={EMAIL_ERROR_TEXT}
            defaultValue={empData[email]}
          />
          <TextInput
            onChange={handleChange}
            id={phoneNumber}
            label="Phone Number"
            cssClass={classes.field}
            error={error[phoneNumber]}
            defaultValue={empData[phoneNumber]}
            helperText={PHONENUMBER_ERROR_TEXT}
          />
          <TextInput
            onChange={handleChange}
            id={message}
            label="Message"
            cssClass={classes.field}
            error={error[message]}
            defaultValue={empData[message]}
            helperText={MIN3CHARS_ERROR_TEXT}
          />
        </Fields>
      </form>
      <YourReferralText style={{ display: "none" }} id={successMessageId}>
        Form submit success
      </YourReferralText>
      <PrimaryButton
        handleClick={() => onClickSubmitButton(data)}
        cssClass={classes.button}
        label="Send"
        // isDisabled={checkSubmitButtonDisability({
        //   error,
        //   data: empData,
        //   modifiedData: data,
        //   noOfFields: 4,
        // })}
      />
    </ModalInputsWrapper>
  );
};

QuoteOrContanctForm.propTypes = {
  onClickCrossIcon: PropTypes.func.isRequired,
  onClickSubmitButton: PropTypes.func.isRequired,
  referral: PropTypes.object.isRequired,
};
export default QuoteOrContanctForm;
