import React, { useState, useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import CloseOutlinedIcon from "@material-ui/icons/CloseOutlined";

import { Heading, PasswordBlock, Fields } from "./styles";
import { handleButtonIsDisabled, handleHasError } from "../../utils/formUtils";

import { TextInput, PrimaryButton } from "../../../CommonComponents";

import { changePassword } from "../../services/ApiService/actions";

const useStyles = makeStyles({
  field: {
    margin: "15px 0",
    width: "350px",
    "& .MuiFilledInput-root ": {
      fontSize: "14px",
      background: `#F0F0F0`,
    },
    "& .MuiList-root ": {
      "z-index": 1000001,
    },
    "& .MuiInputLabel-filled": {
      fontSize: "14px",
    },
  },
  root: {
    top: "30px",
    right: "30px",
    position: "absolute",
    cursor: "pointer",
  },
  button: {
    marginTop: "15px",
    width: "90px",
    fontSize: "14px",
    height: "36px",
  },
});
export const Password = ({ onClickCrossIcon = () => {} }) => {
  const classes = useStyles();

  const [data, setData] = useState({});
  const [error, setError] = useState({});

  const handleError = (id, value) => {
    const hasError = handleHasError(id, value);
    setError((err) => {
      return {
        ...err,
        [id]: hasError,
      };
    });
  };

  const handleChange = (e) => {
    const { id, name, value } = e.target;
    const key = id || name;
    handleError(key, value);
    setData({
      ...data,
      [key]: value,
    });
  };

  const handleClick = async () => {
    await changePassword(data);
    onClickCrossIcon();
  };

  return (
    <PasswordBlock>
      <CloseOutlinedIcon className={classes.root} onClick={onClickCrossIcon} />
      <Heading>Change your password</Heading>
      <Fields>
        <TextInput
          id={"currentPwd"}
          label={"Current password"}
          onChange={handleChange}
          error={error["currentPwd"]}
          cssClass={classes.field}
          type="password"
        />
        <TextInput
          id={"newPwd"}
          label={"New password"}
          onChange={handleChange}
          error={error["newPwd"]}
          cssClass={classes.field}
          type="password"
        />
        <TextInput
          id={"confirmPwd"}
          label={"Confirm password"}
          onChange={handleChange}
          error={error["confirmPwd"]}
          cssClass={classes.field}
          type="password"
        />
      </Fields>
      <PrimaryButton
        handleClick={handleClick}
        label="Update"
        cssClass={classes.button}
        isDisabled={handleButtonIsDisabled(error, 3)}
      />
    </PasswordBlock>
  );
};
