// eslint-disable-next-line import/no-unresolved
import axios from "axios";
import decode from "jwt-decode";
import dayjs from "dayjs";
import history from "../../routes/history";
import createNotification from "../../utils";
import { isAuthenticated } from "../../utils/AuthUtils";

const axiosConfig = {
  baseURL: "https://vinnohrportal.herokuapp.com/",
};

axiosConfig.crossdomain = true;

const axiosInstance = axios.create(axiosConfig);

axiosInstance.interceptors.request.use(function (request) {
  if (!window.navigator.onLine) {
    createNotification(
      "warning",
      "Please make sure you have stable internet connection"
    );
  }

  //set access_token if present
  const accessToken = localStorage.getItem("access_token");
  if (accessToken) {
    if (isAuthenticated()) {
    }
    request.headers.Authorization = `Bearer ${accessToken}`;
    const user = decode(accessToken);
    const isExpired = dayjs.unix(user.exp).diff(dayjs()) < 1;
  } else {
    console.log(window.location);
    history.push("/user-login");
  }

  // if (isExpired) {
  // make refreshToken API call
  // }
  // request.headers.refreshtoken = true;

  return request;
});

axiosInstance.interceptors.response.use(
  (response) => {
    switch (response?.status) {
      case 200:
        const data = response.data;
        if (typeof data === "string") createNotification("success", data);
        break;
      default:
        break;
    }

    return response;
  },
  (error) => {
    switch (error.response?.status) {
      case 401:
        localStorage.removeItem("access_token");
        window.location.replace("/user-login");
        break;
      default:
        break;
    }
    createNotification(
      "error",
      error.response?.data?.message || error.response?.data?.error
    );
    return Promise.reject(error);
  }
);

export default axiosInstance;

