/* eslint-disable no-unused-vars */
import React, { useEffect, useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import CloseOutlinedIcon from "@material-ui/icons/CloseOutlined";

import { PropTypes } from "prop-types";

import {
  checkSubmitButtonDisability,
  handleButtonIsDisabled,
  handleHasError,
} from "../../utils/formUtils";
import {
  EMPLOYEE_DETAILS,
  INVALID_DATE,
  MIN3CHARS_ERROR_TEXT,
} from "../../constants";
import { YourReferralText } from "./styles";
import {
  TextInput,
  PrimaryButton,
  UploadButton,
  DatePicker,
} from "../../../CommonComponents";
import colors from "../../themes/colors";
import { ModalInputsWrapper, Fields } from "..";

const useStyles = makeStyles({
  field: {
    margin: "15px 0",
    width: "100%",
    "& .MuiFilledInput-root ": {
      fontSize: "14px",
      background: `#F0F0F0`,
    },
    "& .MuiList-root ": {
      "z-index": 1000001,
    },
    "& .MuiInputLabel-filled": {
      fontSize: "14px",
    },
  },
  root: {
    top: "30px",
    right: "30px",
    position: "absolute",
    cursor: "pointer",
  },
  button: {
    marginTop: "15px",
    width: "90px",
    fontSize: "14px",
  },
});

const { collegeOrUniversity, higherQualification, startDate, endDate } =
  EMPLOYEE_DETAILS.EDUCATION_IDS;

const EducationForm = ({
  onClickCrossIcon,
  onClickSubmitButton,
  empData = {},
}) => {
  const classes = useStyles();
  const [error, setError] = useState({});
  const [data, setData] = useState(empData);
  const [isDisabled, setIsDisabled] = useState(false);

  const handleError = (id, value) => {
    const hasError = handleHasError(id, value);
    setError((err) => {
      return {
        ...err,
        [id]: hasError,
      };
    });
  };

  const handleChange = (e) => {
    const { id, name, value } = e.target;
    const key = id || name;
    handleError(key, value);
    setData({
      ...data,
      [key]: value,
    });
  };

  const handleClick = () => {};
  return (
    <ModalInputsWrapper>
      <CloseOutlinedIcon className={classes.root} onClick={onClickCrossIcon} />
      <YourReferralText>Education</YourReferralText>
      <Fields>
        <TextInput
          id={higherQualification}
          label="Higher Qualification"
          onChange={handleChange}
          error={error[higherQualification]}
          cssClass={classes.field}
          helperText={MIN3CHARS_ERROR_TEXT}
          defaultValue={empData[higherQualification]}
        />
        <TextInput
          id={collegeOrUniversity}
          label="College or University"
          onChange={handleChange}
          error={error[collegeOrUniversity]}
          cssClass={classes.field}
          helperText={MIN3CHARS_ERROR_TEXT}
          defaultValue={empData[collegeOrUniversity]}
        />
        <DatePicker
          handleChange={handleChange}
          id={startDate}
          label="Start Date"
          cssClass={classes.datePicker}
          defaultValue={empData.startDate}
          helperText={INVALID_DATE}
        />
        <DatePicker
          handleChange={handleChange}
          id={"EndDate"}
          label="End Date"
          cssClass={classes.datePicker}
          defaultValue={empData.endDate}
          helperText={INVALID_DATE}
        />
      </Fields>
      <PrimaryButton
        handleClick={() => onClickSubmitButton(data)}
        cssClass={classes.button}
        label="Submit"
        isDisabled={checkSubmitButtonDisability({
          error,
          data: empData,
          modifiedData: data,
          noOfFields: 4,
        })}
      />
    </ModalInputsWrapper>
  );
};

EducationForm.propTypes = {
  onClickCrossIcon: PropTypes.func.isRequired,
  onClickSubmitButton: PropTypes.func.isRequired,
  referral: PropTypes.object.isRequired,
};
export default EducationForm;
