import { NotificationManager } from "react-notifications";

const createNotification = (type, message) => {
  const modifiedMessage = message;
  switch (type) {
    case "info":
      NotificationManager.info(message);
      break;
    case "success":
      NotificationManager.success("Success message", modifiedMessage);
      break;
    case "warning":
      NotificationManager.warning("NETWORK ERROR", modifiedMessage, 2000);
      break;
    case "error":
      NotificationManager.error(modifiedMessage, "Error", 3000, () => {});
      break;
    default:
      break;
  }
};

export const anchorClickScrollListener = () => {
  setTimeout(() => {
    document.querySelectorAll('a[href^="#"]').forEach((a) => {
      a.addEventListener("click", function (e) {
        e.preventDefault();
        console.log(window.location);
        var href = this.getAttribute("href");
        var elem =
          document.querySelector(href) ||
          document.querySelector(
            "a[name=" + href.substring(1, href.length) + "]"
          ) ||
          "";

        window.scroll({
          top: elem.offsetTop - 40,
          left: 0,
          behavior: "smooth",
        });
      });
    });
  }, 500);
};

export default createNotification;
