import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { get } from "lodash";

import Table from "../../../CommonComponents/Table";
import Modal from "../../../CommonComponents/Modal";

import {
  getDashboardPageJobsList,
  getProjectsList,
} from "../../store/employeeStore/actions";

import { createProject } from "../../services/ApiService/actions";
import { Container } from "../DashboardContainer";
import AlertDialog from "../../../CommonComponents/AlertDialog";

import { deleteJobById } from "../../services/ApiService/actions";
import ProjectsApplicationForm from "../ProjectsApplicationForm";
import { ProjectCard } from "../DashboardProfileView/shared";

const DashboardProjectsView = () => {
  const dispatch = useDispatch();
  const [menuItem, setMenuItem] = useState(null);
  const [selectedProject, setSelectedProject] = useState(null);

  const store = useSelector((state) => get(state, ["employeeStore"], {}));
  const projects = get(store, "projects", {});
  const loader = get(store, "loader", false);

  useEffect(() => {
    dispatch(getProjectsList({ page: 0 }));
  }, []);

  const resetModal = () => {
    setMenuItem(null);
    setSelectedProject(null);
  };

  const createOrUpdateProject = async (data) => {
    await createProject(data);
    dispatch(getProjectsList({ page: 0 }));
    resetModal();
  };

  const getMenuModalContent = () => {
    switch (menuItem) {
      case "Edit":
      case "Add":
        return (
          <ProjectsApplicationForm
            empData={selectedProject}
            onClickCrossIcon={() => {
              resetModal();
            }}
            onClickSubmitButton={createOrUpdateProject}
          />
        );
      default:
        return <ProjectCard  project = {selectedProject} hasClose onClose={resetModal} />;
    }
  };

  const renderDeleteAlert = () => {
    if (menuItem === "Delete") {
      return (
        <AlertDialog
          handleAgreeClick={async () => {
            await deleteJobById({ id: selectedProject.ref });
            await dispatch(getDashboardPageJobsList({ page: 0 }));
            resetModal();
          }}
          handleDisagreeClick={() => {
            resetModal();
          }}
          text="Are you sure, you want to delete this job?"
        />
      );
    }
  };

  const showModal = () => {
    return menuItem === null || menuItem === "Edit" || menuItem === "Add";
  };

  const handleRequestSearch = async (param) => {
    // if (param) {
    //   dispatch(searchEmployeesList({ searchParam: param, page: 0 }));
    // } else {
    //   dispatch(getProjectsList({ page: 0 }));
    // }
  };

  const handleCancelSearch = () => {
    // dispatch(getProjectsList({ page: 0 }));
  };

  return (
    <Container
      loader={loader}
      handleCancelSearch={handleCancelSearch}
      handleRequestSearch={handleRequestSearch}
      handleAddButtonClick={() => {
        setMenuItem("Add");
        setSelectedProject({});
      }}
      noSearchBar
    >
      <>
        {renderDeleteAlert()}
        {showModal() && (
          <Modal
            open={!!(menuItem || selectedProject)}
            handleClose={() => {
              resetModal();
            }}
          >
            {getMenuModalContent()}
          </Modal>
        )}
        <Table
          dashboard
          noCount
          page={projects.number}
          data={projects}
          columnKeys={[
            { id: "projectName", label: "Project Name" },
            { id: "projectDesc", label: "Client" },
            { id: "clientLocation", label: "Location" },
            { id: "startDate", label: "Start Date" },
            { id: "endDate", label: "End Date" },
            { id: "client", label: "Client" },
          ]}
          rowsPerPage={projects.numberOfElements}
          count={projects.totalElements}
          onSelectMenuItem={({ menu, row }) => {
            setMenuItem(menu);
            setSelectedProject(row);
          }}
          onSelectTableRow={(row) => {
            setSelectedProject(row);
          }}
          gotoNextPage={(page) => {
            dispatch(getDashboardPageJobsList({ page }));
          }}
          handleCountClick={({ row, menu }) => {
            setMenuItem(menu);
            setSelectedProject(row);
          }}
        />
      </>
    </Container>
  );
};

export default DashboardProjectsView;
