import React, { useState } from "react";
import styled from "styled-components";
import { makeStyles } from "@material-ui/core/styles";
import { debounce } from "lodash";
import CircularProgress from "@material-ui/core/CircularProgress";
import SearchBarComponent from "../SearchBarComponent";
import PrimaryButton from "../../../CommonComponents/PrimaryButton";

export const ViewContainer = styled.div`
  padding: 20px 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  box-shadow: none !important;
  margin-bottom: unset !important;
`;

export const LoaderContainer = styled.div`
  height: calc(100vh - 180px);
  display: flex;
  justify-content: center;
  align-items: center;
`;

const useStyles = makeStyles({
  root: {
    fontSize: "14px",
    textTransform: "none",
  },
});

export const Container = (props) => {
  const {
    children,
    loader,
    handleCancelSearch,
    handleRequestSearch,
    handleAddButtonClick,
    noSearchBar,
    noAddButton,
  } = props;
  const [filter, setFilter] = useState("");
  const classes = useStyles();

  const onRequestSearch = (text) => {
    setFilter(text);
    handleRequestSearch(text);
  };

  const handleSearchDebounce = debounce(onRequestSearch, 500);

  const onCancelSearch = () => {
    setFilter("");
    handleCancelSearch();
  };

  return (
    <ViewContainer loader={loader}>
      <div
        style={{
          width: "90%",
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
          padding: "20px 0",
        }}
      >
        {!noSearchBar ? (
          <SearchBarComponent
            onCancelSearch={onCancelSearch}
            onRequestSearch={handleSearchDebounce}
            text={filter}
          />
        ) : (
          <div></div>
        )}
        {!noAddButton ? (
          <PrimaryButton
            className={classes.root}
            handleClick={handleAddButtonClick}
            label="Add"
          />
        ) : (
          <div></div>
        )}
      </div>
      {loader ? (
        <LoaderContainer>
          <CircularProgress />
        </LoaderContainer>
      ) : (
        children
      )}
    </ViewContainer>
  );
};
