import React, { useEffect, useState } from "react";
import { get } from "lodash";
import { useSelector } from "react-redux";

import empty from "../../../assets/svgs/empty.svg";

import MainContainer from "../../components/MainContainer";
import {
  ADMIN_ROLE,
  HR_ROLE,
  RECRUITER_ROLE,
  EMPLOYEE_ROLE,
} from "../../constants";
import DashboardJobsView from "../../components/DashboardJobsView";
import DashboardCoursesView from "../../components/DashboardCoursesView";
import DashboardEmployeesView from "../../components/DashboardEmployeesView";
import DashboardProfileView from "../../components/DashboardProfileView";
import DashboardProjectsView from "../../components/DashboardProjectsView";
import DashboardRequestQuoteDetails from "../../components/DashboardRequestQuoteDetails";
import DashboardContacts from "../../components/DashboardContacts";

import {
  RightContentContainer,
  EmptyIconContainer,
  EmptyDescription,
  LeftContentContainer,
  LeftContent,
  TabItemContainer,
} from "./styles";

import {
  headers,
  PROFILE_TAB,
  EMPLOYEES_TAB,
  CAREER_DETAILS_TAB,
  TRAININGS_TAB,
  PROJECTS_TAB,
  QUOTE_DETAILS,
  CONTACTS,
} from "./shared";

const Dashboard = () => {
  const [selectedTab, setSelectedTab] = useState(PROFILE_TAB.id);
  const store = useSelector((state) => get(state, ["employeeStore"], {}));
  const profile = get(store, "profile", {});
  const { accRole } = profile;

  const renderEmptyView = () => {
    return (
      <EmptyIconContainer>
        <img src={empty} alt="" style={{ width: "300px", height: "300px" }} />
        <EmptyDescription>Page is under development</EmptyDescription>
      </EmptyIconContainer>
    );
  };

  const renderContent = () => {
    switch (selectedTab) {
      case CAREER_DETAILS_TAB.id:
        return <DashboardJobsView />;
      case TRAININGS_TAB.id:
        return <DashboardCoursesView />;
      case EMPLOYEES_TAB.id:
        return <DashboardEmployeesView />;
      case PROFILE_TAB.id:
        return <DashboardProfileView />;
      case PROJECTS_TAB.id:
        return <DashboardProjectsView />;
      case QUOTE_DETAILS.id:
        return <DashboardRequestQuoteDetails />;
      case CONTACTS.id:
        return <DashboardContacts />;
      default:
        return renderEmptyView();
    }
  };

  const renderRightContent = () => {
    return <RightContentContainer>{renderContent()}</RightContentContainer>;
  };

  const getHeaders = () => {
    switch (accRole) {
      case ADMIN_ROLE:
        return headers;
      case HR_ROLE:
        return [
          PROFILE_TAB,
          EMPLOYEES_TAB,
          PROJECTS_TAB,
          CAREER_DETAILS_TAB,
          TRAININGS_TAB,
        ];
      case RECRUITER_ROLE:
        return [PROFILE_TAB, EMPLOYEES_TAB, CAREER_DETAILS_TAB, TRAININGS_TAB];
      case EMPLOYEE_ROLE:
        return [PROFILE_TAB];
      default:
        return [PROFILE_TAB];
    }
  };

  const renderLeftContent = () => {
    return (
      <LeftContentContainer>
        <LeftContent>
          {getHeaders().map((header) => (
            <TabItemContainer
              key={header.id}
              selected={selectedTab === header.id}
              onClick={() => {
                setSelectedTab(header.id);
              }}
            >
              <span
                style={{
                  marginLeft: "6px",
                  fontSize: "16px",
                  fontWeight: "bold",
                }}
              >
                {header.label}
              </span>
            </TabItemContainer>
          ))}
        </LeftContent>
      </LeftContentContainer>
    );
  };

  return (
    <MainContainer loadingStatus={200} noScroll isDashboard>
      <div style={{ display: "flex" }}>
        {renderLeftContent()}
        {renderRightContent()}
      </div>
    </MainContainer>
  );
};

export default Dashboard;
